import React from "react";
import PropTypes from "prop-types";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";
import _ from "lodash";

import {getClientContext, getSelectedOfferType} from "eshop/modules/configurator/selectors/filters";
import {changeSearchDeviceValue, reloadProductList} from "eshop/modules/simfree/actions/filter";
import {getCurrentDeviceInstalmentsCountValue} from "eshop/modules/configurator/selectors/offers";
import {getSearchValue, getSelectedBaseDeviceCode, isProductDetailsPage} from "eshop/modules/simfree/selectors";
import DevicesInstalmentSelector from "eshop/modules/magnum2/components/DeviceList/DevicesInstalmentSelector";
import {getAddTerminalToOfferBundleNo} from "eshop/modules/cart/selectors";
import {selectConvergentOffer} from "../../actions/app";

import {selectProposition, setLoyaltyPeriod} from "../../../magnum2/actions/magnum";
import {
    transactionsToSelectOptions,
    transactionsToSelectOptionsForSimpleMobile
} from "../../../magnum2/helpers/TransactionHelper";
import {is2U, is4U, isLTE} from "../../../magnum2/components/BundleTypeUtils";
import {
    getMagnumType,
    getPossibleFixBroadbandTransactions,
    getPossibleFixVoipTransactions,
    getPossibleMobileTransactions,
    getSelectedFixBroadbandTransactionOption,
    getSelectedFixVoipTransactionOption,
    getSelectedMobileTransactionOption,
    getTransactions
} from "../../../magnum2/selectors";

import MagnumOfferPropositionListItem from "./view/MagnumOfferPropositionListItem";
import MagnumOfferPropositionListItemProduct from "./view/MagnumOfferPropositionListItemProduct";
import OfferType from "../../constants/OfferTypeEnum";
import SelectWithFloatingLabel from "../../../magnum2/components/SelectWithFloatingLable";
import LabeledInput from "../../../core/components/hoc/LabeledInput";
import MagnumPropositionListPriceTag from "./view/MagnumPropositionListPriceTag";
import PromotionType from "../../constants/PromotionType";
import ModalService from "../../../../flux/utils/OraModalService";
import OfferDetailsModal from "eshop/modules/cart/components/entriesList/shared/productDetails/OfferDetailsModal";
import OnlineUtils from "eshop/utils/OnlineUtils";
import InfoComponent from "eshop/components/InfoComponent";

class MagnumFilterComponent extends React.Component {

    constructor(props) {
        super(props);
        this.onSelectProposition = this.onSelectProposition.bind(this);
        this.renderPropositionListForShopPage = this.renderPropositionListForShopPage.bind(this);
        this.renderPropositionListForProductCard = this.renderPropositionListForProductCard.bind(this);
        this.isProductCard = this.isProductCard.bind(this);
        this.changeSearchDeviceValue = this.changeSearchDeviceValue.bind(this);
        this.renderSearchDevicesByNameOrSerialNumber = this.renderSearchDevicesByNameOrSerialNumber.bind(this);
    }

    componentDidMount() {
        this.registerModalServiceSelectedPropositionDetails();
        this.initUIModules();
    }
    componentDidUpdate() {
        this.registerModalServiceSelectedPropositionDetails();
        this.initUIModules();
    }

    registerModalServiceSelectedPropositionDetails() {
        const proposition = this.props.selectedProposition;
        if (proposition) {
            ModalService.add((data) => {
                return (
                    <OfferDetailsModal
                        id={"offer_proposition_details_" + proposition.code}
                        header={"Szczegóły"}
                        broadband={proposition.broadband}
                        tv={proposition.tvProduct}
                        tvPackages={proposition.tvPackages}
                        voip={proposition.voip}
                        voice={proposition.voice}
                        data={proposition.data}
                        transactions={this.props.transactions}
                    />)
            });
        }
    }

    initUIModules() {
        let $dispositionDetailsArea = document.getElementById("magnum-filter-Component-area");
        OPL.UI.initModules($dispositionDetailsArea);
    }

    openDetailsModal(propositionCode) {
        ModalService.open("offer_proposition_details_" + propositionCode);
    };

    shouldBeVisible(offerType) {
        return offerType === OfferType.CONVERGENT;
    }

    onSelectLoyaltyPeriod(option) {
        this.props.setLoyaltyPeriod(option.target.value);
    }

    onSelectProposition(proposition) {
        this.props.selectProposition(proposition);
        if(this.props.isProductDetailsPage) {
            this.props.selectConvergentOffer(proposition.mobileVoiceBundleTemplateCode);
        } else {
            this.props.reloadProductList();
        }
    }

    changeSearchDeviceValue(e) {
        this.props.changeSearchDeviceValue(e.value);
    }

    isProductCard(){
        return this.props.selectedBaseDeviceCode !== "";
    }

    renderPropositionList() {
        if (!this.props.durationList) {
            return;
        }
        let secondaryChoiceOffer = this.props.durationList.secondaryChoiceOffer;
        let propositions = this.props.durationList.propositions.filter( proposition => secondaryChoiceOffer === false || ( secondaryChoiceOffer === proposition.availableAsSecondaryChoiceOffer ) );
        let propositionGroups = [
            propositions.filter(proposition => !proposition.promotionType),
            propositions.filter(proposition => proposition.promotionType === PromotionType.MINI),
            propositions.filter(proposition => proposition.promotionType === PromotionType.STANDARD),
            propositions.filter(proposition => proposition.promotionType === PromotionType.EXTRA),
            propositions.filter(proposition => proposition.promotionType === PromotionType.PREMIUM)
        ];
        return (
            <div>
                {
                    propositionGroups
                        .filter(propositions => propositions.length > 0)
                        .map((propositions, index) => {
                            return (
                                <div key={propositions[0].promotionType}>
                                    {index !== 0 && this.renderSeparator()}
                                    {this.renderPropositions(propositions)}
                                </div>
                            )
                        })

                }
            </div>
        );
    }

    renderSeparator() {
        return (
            <div className="u-padding-bottom-m u-padding-right-m u-padding-left-m">
                <div className="o-separator o-separator--m" style={{borderColor: "#202013"}}/>
            </div>
        );
    }

    renderPropositions(propositions) {
        return propositions.map(proposition => {
            const offerPriceVariant = this.props.knaNumber ? proposition.priceVariants.ACTIVATION_ACTIVE_WLR : proposition.priceVariants.DEFAULT;
            return (<MagnumOfferPropositionListItem
                selected={proposition.code === this.props.selectedProposition.code}
                key={proposition.code}
                offer={offerPriceVariant}
                offerDescription={this.resolveOfferDescription(proposition)}
                onChange={() => this.onSelectProposition(proposition)}
                descriptions={this.props.descriptions}
                promotionType={proposition.promotionType}
            />);
        });
    }

    renderPropositionForProductCart() {
        if (this.props.addTerminalToOfferBundleNo || !this.props.durationList) {
            return;
        }
            const offerPriceVariant = this.props.knaNumber ? this.props.selectedProposition.priceVariants.ACTIVATION_ACTIVE_WLR : this.props.selectedProposition.priceVariants.DEFAULT;
            return (<MagnumOfferPropositionListItemProduct
                selected={true}
                selectedProposition={this.props.selectedProposition}
                offer={offerPriceVariant}
                offerDescription={this.resolveOfferDescription(this.props.selectedProposition)}
                descriptions={this.props.descriptions}
                disabled={false}
                transactions={this.props.transactions}
            />);
    }

    resolveOfferDescription(proposition) {
        if(is4U(this.props.availableBundleTypes) || is2U(this.props.availableBundleTypes)) {
            return this.getDescriptionFromFeatures(proposition.broadband) || "Orange Love";
        } else if (isLTE(this.props.availableBundleTypes)) {
            return "Orange Love " + _.capitalize(proposition.promotionType);
        } else {
            return "";
        }
    }

    getDescriptionFromFeatures(product) {
        const features = product.features;
        if (features && features.length > 0 && features[0].values && features[0].values.length > 0) {
            return features[0].values[0];
        } else {
            return undefined;
        }
    }

    renderDurationOptions() {
        let durationOptions = [];

        if(this.props.durationList) {
            durationOptions = this.props.durationList.durations.map(duration => {
                return {
                    value: duration.value,
                    label: duration.displayName
                };
            });
        }
        return (
            <div className={this.isProductCard()? "l-col l-col-small-12 l-col-medium-6 l-col-6 ": ""}>
                <SelectWithFloatingLabel
                    id="loyaltyPeriodSelect"
                    options={durationOptions}
                    placeholder={this.props.descriptions.loyaltyPeriodPlaceholder}
                    value={this.props.selectedLoyaltyPeriod}
                    onChange={this.onSelectLoyaltyPeriod}
                    disabled={true}
                />
            </div>
        );
    }

    renderPropositionListForShopPage() {
        let offer = this.props.knaNumber ? this.props.selectedProposition.priceVariants.ACTIVATION_ACTIVE_WLR : this.props.selectedProposition.priceVariants.DEFAULT;
        let selectedPropositionCode = this.props.selectedProposition.code;
        let propositionName = "Orange Love " + _.capitalize(this.props.selectedProposition.promotionType);
        if (this.props.magnumType === OfferType.VOICE && this.props.selectedProposition && this.props.selectedProposition.voice) {
            propositionName = this.props.selectedProposition.voice.name;
        }
        return(
            <div>
                <div className="u-padding-all-m u-no-padding-top">
                    <InfoComponent color={"blue"} bgColor={"none"} textColor={"black"}>
                        <b>{this.props.descriptions.discountDescription}</b>
                    </InfoComponent>
                    {this.props.magnumType === OfferType.CONVERGENT &&
                        (<p className="h4 u-spacing u-padding-top-m">
                            {this.props.descriptions.smallTitle}
                        </p>)
                    }
                    <p className="h5 u-spacing">
                        {propositionName}
                    </p>
                    <div className="l-row">
                        <div className="l-col">
                            <div className="g-brand2-c u-font-bold u-margin-s">
                                <MagnumPropositionListPriceTag
                                    key={"key_" + "convergent" + "MagnumPropositionListPriceTag"}
                                    clientContext={true}
                                    loyaltyLength={this.props.selectedDeviceInstalmentsCountValue}
                                    mainPriceClass={"g-brand1-c"}
                                    alignClass="u-left"
                                    offer={this.props.knaNumber ? this.props.selectedProposition.priceVariants.ACTIVATION_ACTIVE_WLR : this.props.selectedProposition.priceVariants.DEFAULT}
                                    offerDiscountInfoDescription={this.props.descriptions.offerDiscountInfoDescription}
                                    offerDiscountInfoConvDescription={this.props.descriptions.offerDiscountInfoConvDescription}
                                    baseId="desktop"
                                    availableBundleTypes={this.props.availableBundleTypes}
                                />
                            </div>
                        </div>
                    </div>
                    <div className="l-row">
                        <div className="l-col">
                            <span className="u-left u-small-left">
                                <span className={"g-brand1-c u-font-bold"}>{OnlineUtils.formatPrice(offer.payNowPrice.value) + " " + offer.currency + " aktywacja"}</span>
                            </span>
                        </div>
                    </div>
                </div>

                <div data-js-module="common/modules/opl-expander" data-js-options="{&quot;scrollToSelected&quot;: false, &quot;scrollTohash&quot;: true, &quot;hideOtherElements&quot;:true,&quot;triggerSelector&quot;:&quot;.opl-content-trigger&quot;,&quot;contentSelector&quot;:&quot;.opl-section__content&quot;,&quot;parentClass&quot;:&quot;opl-section--togglable&quot;, &quot;nextSelector&quot;:&quot;.opl-section__expander-next&quot;}">
                    <div className="opl-section opl-section--togglable opl-section--inner-cut u-no-padding">
                    <div className="js-expander__container u-margin-s">
                            <div className="l-row u-padding-all-m u-no-padding-top u-no-padding-b">
                                <div className="l-col u-spacing-top-s">
                                    <div className="u-inline-block u-spacing-right u-padding-b">
                                        <a href="#" className="u-inline-block u-spacing-right" onClick={() => this.openDetailsModal(selectedPropositionCode)}>
                                            {this.props.descriptions.detailsLabel}
                                        </a>
                                        <span className="u-acc-hide">"Szczegóły oferty"</span>
                                    </div>
                                    {!this.props.addTerminalToOfferBundleNo && (
                                        <a href="#" role="tab" aria-controls="expander-content-magnum" aria-expanded="false" className="opl-content-trigger u-inline-block opl-section__expander-trigger-btn u-float-none">
                                            <span className="opl-section__expander-trigger--show">Zmień</span>
                                            <span className="opl-section__expander-trigger--hide">Zwiń</span>
                                        </a>
                                    )}
                                </div>
                            </div>
                        </div>
                        <div id="expander-content-magnum" aria-expanded="false" className="opl-section__content">
                            <ul className="opl-multicheckout-services">
                                {this.renderPropositionList()}
                            </ul>
                        </div>
                    </div>
                </div>
                </div>
        )
    }

    renderPropositionListForProductCard() {
        return(
            <ul className="opl-switcher-list g-gray1-bg">
                <li className="opl-switcher-list__item u-small-hide u-no-padding-l u-no-padding-r">
                    <div className="l-row u-no-margin">
                        <div className="l-col l-col-1 "/>
                        <div className="l-col l-col-11 ">
                            <div className="l-row u-display_table u-small-block u-no-margin">
                                <div className="l-col l-col-6  u-display_table-cell u-small-block u-vertical-top">
                                    <p className="h4 u-spacing">
                                        {this.props.descriptions.smallTitle}
                                    </p>
                                </div>
                                <div className="l-col l-col-small-12 l-col-medium-3 l-col-3  u-display_table-cell u-small-block u-vertical-top u-text-left">
                                    <p>Abonament miesięcznie</p>
                                </div>
                                <div className="l-col l-col-small-12 l-col-medium-3 l-col-3  u-display_table-cell u-small-block u-vertical-top u-text-right">
                                    <p>Aktywacja</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </li>
                {this.renderPropositionForProductCart()}
            </ul>
        )
    }

    renderSearchDevicesByNameOrSerialNumber() {
        return(
            <div className={this.props.channels.sales === 'WWW' ? "u-hide" : "opl-console u-margin-m u-padding-right-l"}>
                <label>{this.props.descriptions.searchByValueLabel || "Wyszukaj wg nazwy lub indeksu materiałowego:"}</label>
                <div className="opl-input-with-icon">
                    <LabeledInput className="g-white1-bg" onChange={this.changeSearchDeviceValue} value={this.props.searchValue} id="search-by-value-mobile"/>
                    <span className="g-icon g-icon--only g-icon--s g-icon--search"/>
                </div>
            </div>
        )
    }

    render() {
        const fixBroadbandPossibleTransactionsOptions = transactionsToSelectOptions(this.props.fixBroadbandPossibleTransactions, this.props.descriptions);
        const fixVoipPossibleTransactionsOptions = transactionsToSelectOptions(this.props.fixVoipPossibleTransactions, this.props.descriptions);
        const mobilePossibleTransactionsOptions = this.props.magnumType === OfferType.CONVERGENT ?
         transactionsToSelectOptions(this.props.mobilePossibleTransactions, this.props.descriptions) :
         transactionsToSelectOptionsForSimpleMobile(this.props.mobilePossibleTransactions, this.props.descriptions);

        const voipSelectorVisible = is4U(this.props.availableBundleTypes) && this.props.fixVoipPossibleTransactions.length > 1;

        return (
            <div id="magnum-filter-Component-area" className={this.shouldBeVisible(this.props.selectedOfferType) ? "" : "u-hide"}>
                {(this.props.durationList.propositions.length === 0) ? <p className="h4 u-spacing-l u-padding-all-m">{this.props.descriptions.disabledInfo}</p> :
                    <div className="u-spacing-l">
                        <div className="g-gray1-bg">
                            <div className="u-padding-all-m">
                            <div className={this.isProductCard()? "l-col l-col-small-12 l-col-medium-6 l-col-6 ": ""}>
                                <SelectWithFloatingLabel
                                    id="fixBroadbandOptionsSelect"
                                    options={fixBroadbandPossibleTransactionsOptions}
                                    placeholder={this.props.descriptions.fixBroadbandTransactionsPlaceholder}
                                    value={this.props.selectedFixBroadbandTransactionOption}
                                    disabled={true}
                                />
                            </div>

                            <div className={this.isProductCard()? "l-col l-col-small-12 l-col-medium-6 l-col-6 ": ""}>
                                <SelectWithFloatingLabel
                                    id="mobileOptionsSelect"
                                    options={mobilePossibleTransactionsOptions}
                                    placeholder={this.props.descriptions.mobileTransactionsPlaceholder}
                                    value={this.props.selectedMobileTransactionOption}
                                    disabled={true}
                                />
                            </div>
                            {voipSelectorVisible && (
                                <div className={this.isProductCard()? "l-col l-col-small-12 l-col-medium-6 l-col-6 ": ""}>
                                    <SelectWithFloatingLabel
                                        id="fixVoipOptionsSelect"
                                        options={fixVoipPossibleTransactionsOptions}
                                        placeholder={this.props.descriptions.fixVoipTransactionsPlaceholder}
                                        value={this.props.selectedFixVoipTransactionOption}
                                        disabled={true}
                                    />
                                </div>
                            )}
                            {this.renderDurationOptions()}
                            {<div className={this.isProductCard()? "l-col l-col-small-12 l-col-medium-6 l-col-6 ": ""}>
                                 <DevicesInstalmentSelector onChange={ (option) => this.props.reloadProductList(option.target.value)}/>
                                </div>
                            }
                            </div>
                            {this.isProductCard() ? this.renderPropositionListForProductCard():
                                this.renderPropositionListForShopPage() }
                        </div>

                        {!this.isProductCard() ? this.renderSearchDevicesByNameOrSerialNumber() : null}
                    </div>
                }
            </div>
        );
    }
}

MagnumFilterComponent.propTypes = {
    descriptions: PropTypes.shape({}),
    durationList: PropTypes.shape({
        durations: PropTypes.arrayOf(PropTypes.shape({})),
        propositions: PropTypes.arrayOf(PropTypes.shape({
            broadband: PropTypes.object,
            tvProduct: PropTypes.object,
            voip: PropTypes.object,
            voice: PropTypes.object,
            data: PropTypes.object
        }))
    })
};

const mapStateToProps = state => ({
    selectedOfferType: getSelectedOfferType(state),
    availableBundleTypes: state.magnum.availableBundleTypes,
    selectedProposition: state.magnum.selectedProposition,
    durationList: state.magnum.durationList,
    selectedLoyaltyPeriod: state.magnum.selectedLoyaltyPeriod,
    fixBroadbandPossibleTransactions: getPossibleFixBroadbandTransactions(state),
    fixVoipPossibleTransactions: getPossibleFixVoipTransactions(state),
    mobilePossibleTransactions: getPossibleMobileTransactions(state),
    selectedFixBroadbandTransactionOption: getSelectedFixBroadbandTransactionOption(state),
    selectedFixVoipTransactionOption: getSelectedFixVoipTransactionOption(state),
    selectedMobileTransactionOption: getSelectedMobileTransactionOption(state),
    knaNumber: state.magnum.knaNumber,
    isProductDetailsPage: isProductDetailsPage(state),
    selectedDeviceInstalmentsCountValue: getCurrentDeviceInstalmentsCountValue(state),
    selectedBaseDeviceCode: getSelectedBaseDeviceCode(state),
    searchValue: getSearchValue(state),
    clientContext: getClientContext(state),
    addTerminalToOfferBundleNo: getAddTerminalToOfferBundleNo(state),
    magnumType: getMagnumType(state),
    transactions: getTransactions(state)
});


const mapDispatchToProps = dispatch => ({
    ...bindActionCreators({
        selectProposition,
        reloadProductList,
        setLoyaltyPeriod,
        selectConvergentOffer
    }, dispatch),
    changeSearchDeviceValue: (value) => dispatch(changeSearchDeviceValue(value)),
});


export default MagnumFilterComponent = connect(mapStateToProps, mapDispatchToProps)(MagnumFilterComponent);